<script lang="coffeescript">var destroyListItem, dispatch, getRootItems, goToItem, jump, radarList, refresh, resource, style, updateAttribute, updateListItem;

import {
  onMount,
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import {
  push,
  pop,
  replace,
  location
} from 'svelte-spa-router';

import Indent from "../_Common/Indent.svelte";

import ListItems from './Sections/ListItems.svelte';

import Menu from "../_Common/Menus/List.svelte";

import InlineInput from '../../components/Form/InlineInput/InlineInput.svelte';

import ProgressPopover from '../_Common/Popovers/Progress.svelte';

import DueDatePopover from '../_Common/Popovers/DueDate.svelte';

import PageContent from '../_Common/PageContent.svelte';

import PageHeader from '../_Common/PageHeader.svelte';

import RootList from '../../helpers/root_item_list.js';

jump = function(anchorName) {
  var elem, scrollContainer;
  elem = u(`[name=${anchorName}]`);
  scrollContainer = u("body > main > section").first();
  return scrollContainer.scrollTop = elem.first().offsetTop - 40;
};

list = [];

rawList = [];

resource = new RootList();

getRootItems = function() {
  var projectLoadedScb;
  projectLoadedScb = function(projects) {
    list = projects;
    // console.log projects
    return rawList = projects.map(function(p) {
      return Object.select(p, [
        "id",
        "assignments" // this creates a discrete copy of the object. array.clone() does not do that.
      ]);
    });
  };
  return resource.select(projectLoadedScb, null, null);
};

getRootItems();

destroyListItem = function(e) {};

export var list = [];

export var rawList = [];

style = ":hover { font-weight: bold }";

updateAttribute = function(e) {
  var found;
  // Update Project Attribute
  console.log("HomeProjectList.updateAttribute");
  console.log(e.detail);
  found = list.find({
    id: e.detail.id
  });
  if (found != null) {
    Object.merge(found, e.detail);
    return resource.patch(found.pouchify(), getRootItems);
  }
};

goToItem = function(p) {
  console.log("gotoitem");
  console.log(`#/lists/${p.id}`);
  return push(`#/lists/${p.id}`);
};

refresh = function() {
  return dispatch("refresh");
};

updateListItem = function(e) { // update a child in the list_items hierarchy
  var change, found, originalItem, originalItemIndex, originalItemReference, scb;
  change = e.detail;
  console.error("updateListItem");
  // change.list_id
  found = resource.items.find({
    id: change.list_id
  });
  console.debug(found);
  originalItemReference = found.reference_from_ancestry(change.ancestry);
  originalItemIndex = originalItemReference.list_items.findIndex({
    uuid: change.uuid
  });
  originalItem = originalItemReference.list_items[originalItemIndex];
  console.debug(change);
  Object.merge(originalItem, change);
  scb = function(items) {};
  console.error("CHANGED ITEM");
  console.debug(found);
  return resource.patch(found.pouchify(), getRootItems);
};

//refreshAndUpdate()
radarList = function(list) {
  var output;
  //output = list.filter (i)->
  //  return i.has_due_dates() == true
  output = [];
  list.forEach(function(i) {
    if (i.has_due_dates()) {
      i.radar_tree();
      return output.append(i);
    }
  });
  //  console.log i
  //console.error "radarList"
  // console.debug output

  console.error("radarList");
  console.debug(output);
  // return []
  return output.sortBy("index");
};

$: sortedList = radarList(list);
</script>


<svelte:head>
  <title>Radar :: Ongoing</title>
</svelte:head>

<PageContent>
  <a name="top"></a>
  <PageHeader>Lists with Ongoing Items</PageHeader>

  <!--
  <div>
    <label for="select">Horizon</label>
    <select>
      <option>0 days</option>
      <option>1 day</option>
      <option>7 days</option>
      <option>14 days</option>
      <option>1 month</option>
      <option>3 months</option>
      <option>6 months</option>
      <option>12 months</option>
      <option>All</option>
    </select>

  </div>
  -->

  <section>
    <div>
      {#if sortedList.length > 0}
        {#each sortedList as list }

          <section>
            <div layout="row" type="list-item">
              <div flex="initial" section="progress" style="padding-right: 8px; padding-top: 3px">
                <ProgressPopover item={list} bind:value={list.progress} on:update={updateAttribute} on:open on:close idAttribute="id"/>
              </div>

              <div flex ellipsis >
                <InlineInput bind:value={list.name} on:update={ updateAttribute({ detail: {id: list.id, name: list.name} }) } on:clicked={goToItem(list)} {style} let:label>
                  <h3 ellipsis><a href="#/lists/{list.id}" on:click|preventDefault><em>{label || "No Name"}</em></a></h3>
                </InlineInput>
              </div>
              <div flex="initial" style="padding-left: 8px; padding-top: 3px">
                <div layout="row">
                  <div flex="initial" present={list.due_date != null}>
                    <DueDatePopover item={list} value={list.due_date} on:update={updateAttribute} on:open on:close/>
                  </div>
                </div>
              </div>
            </div>

            {#if list.radar_items.length > 0}
              <ListItems bind:list={list} on:destroy={destroyListItem} on:update={updateListItem} listId={list.id}/>
            {:else}
              While the list has a date attached, no list items in this list with date assigned.
            {/if}




          </section>

        {/each}
      {:else}
        <section>
          <p>You currently have no lists.</p>
          <p>Go to the <a href="#/lists">Lists</a> view and assign some due dates to see some content here.</p>
        </section>
      {/if}

    </div>

  </section>
</PageContent>
