<script lang="coffeescript">var checkTypesForListId, dispatch, drop, isInvalid, isOver, leave, over;

import {
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

export var index = void 0;

export var item = {
  is_divider: true
};

export var listId = void 0;

export var sameListOnly = false;

isOver = false;

isInvalid = false;

checkTypesForListId = function(types) {
  return types.find(function(t) {
    return t.startsWith("listid:");
  });
};

over = function(e) {
  var draggedListId;
  e.preventDefault();
  e.stopPropagation();
  isOver = true;
  isInvalid = false;
  if (!!sameListOnly) {
    draggedListId = checkTypesForListId(e.dataTransfer.types);
    if (draggedListId != null) {
      return isInvalid = !(listId === draggedListId.replace("listid:", ""));
    } else {
      return isInvalid = true;
    }
  }
};

//if isInvalid
//  return e.preventDefault()
//console.log "return is invalid: #{!isInvalid}"
// return !isInvalid
leave = function(e) {
  var draggable;
  draggable = false;
  isOver = false;
  return isInvalid = false;
};

drop = function(e) {
  var dragItem, draggable, dropped;
  isOver = false;
  isInvalid = false;
  draggable = false;
  e.preventDefault();
  dragItem = JSON.parse(e.dataTransfer.getData("text/plain"));
  dropped = true;
  if (!!sameListOnly) {
    // console.log "This list: #{listId} :: itemList #{dragItem.listId}"
    dropped = listId === dragItem.listId;
  }
  if (!Object.has(item, "is_divider")) {
    item["is_divider"] = true;
  }
  console.log("DroppableDivider - drop");
  console.log(item);
  if (dropped) {
    return dispatch("drop", {
      divider: true,
      dragItem: dragItem,
      dragIndex: dragItem.index,
      dragListId: dragItem.listId,
      dropItem: item,
      dropIndex: index,
      dropListId: listId
    });
  }
};
</script>
<style lang="sass">div {
  border-radius: 3px;
  width: calc(100% + 96px);
  margin-left: -52px;
  background: transparent;
  height: 8px; }
  div.over {
    background: #dddddd; }
  div.invalid {
    background: transparent; }

/*# sourceMappingURL=DroppableDivider.svelte.css.map */</style>
<div
  class:over={isOver}
  class:invalid={isInvalid}
  {index}
  on:drop={drop}
  on:dragover={over}
  on:dragleave={leave}
/>
