<script lang="coffeescript">var add, collapse, dispatch, drop, hover, isHover, isInvalid, isOver, leave, over, toggleExpanded, unhover;

import {
  createEventDispatcher
} from "svelte";

import {
  slide
} from "svelte/transition";

import {
  sineIn,
  sineOut
} from 'svelte/easing';

import QuickEntry from "../../../_Common/QuickEntry/GenericItem.svelte";

dispatch = createEventDispatcher();

import Indent from "../Indent.svelte";

export var index = void 0;

export var indentation = 0;

export var item = {
  is_divider: true
};

export var listId = void 0;

export var firstItem = false;

isOver = false;

isHover = false;

isInvalid = false;

export var isExpanded = false;

over = function(e) {
  e.preventDefault();
  e.stopPropagation();
  isOver = true;
  return isInvalid = false;
};

// console.error "OVER"

//if isInvalid
//  return e.preventDefault()
//console.log "return is invalid: #{!isInvalid}"
// return !isInvalid
hover = function(e) {
  e.preventDefault();
  e.stopPropagation();
  if (!isOver) {
    return isHover = true;
  }
};

// console.error "HOVER"
unhover = function(e) {
  e.preventDefault();
  e.stopPropagation();
  if (!isOver) {
    // console.log "UNHOVER"
    return isHover = false;
  }
};

leave = function(e) {
  var draggable;
  draggable = false;
  isOver = false;
  return isInvalid = false;
};

// console.error "LEAVE"
// isHover = false
add = function(e) {
  var toIndex;
  e.stopPropagation();
  toIndex = item.index;
  if (firstItem) {
    if (index) { // takes priority
      toIndex = index;
    } else {
      toIndex = 0;
    }
  }
  isHover = false;
  isExpanded = false;
  console.error("divider add");
  console.debug({
    ancestry: item.ancestry,
    index: toIndex
  });
  return dispatch("add", {
    ancestry: item.ancestry,
    index: toIndex
  });
};

drop = function(e) {
  var dragItem, draggable, dropped, eventType;
  isOver = false;
  isInvalid = false;
  draggable = false;
  eventType = "drop";
  //console.log "NestableDivider.svelte"
  e.preventDefault();
  dragItem = JSON.parse(e.dataTransfer.getData("text/plain"));
  //console.log item

  //console.log "--------------------------------"
  if (!firstItem) {
    if (item.list_items.length > 0) {
      console.log("------------------------------ DIVIDER DROP");
      console.log("dragitem");
      console.debug(dragItem);
      console.log("dropitem");
      console.log(item);
      console.log(`item.list_items.length ${item.list_items.length} :: nest`);
      eventType = "nest";
      item["to_index"] = 0;
    } else {
      console.log(`DROP ITEM ${item.name} index: ${item.index}`);
      item["to_index"] = item.index + 1;
    }
    // console.log "evntType: #{eventType}"
    dropped = true;
    if (!Object.has(item, "is_divider")) {
      item["is_divider"] = true; // handle first item
    }
  } else {
    //console.log "DroppableDivider - drop"
    //console.log item
    console.error("FRIST ITEM");
    item["to_index"] = 0;
    console.log(dragItem);
    console.log(`eventType ${eventType}`);
    dropped = true;
  }
  if (dropped) {
    return dispatch(eventType, {
      divider: true,
      dragItem: dragItem,
      dragIndex: dragItem.index,
      dragListId: dragItem.listId,
      dropItem: item,
      dropIndex: index,
      dropListId: listId
    });
  }
};

toggleExpanded = function() {
  console.log("toggleExpanded");
  return isExpanded = !isExpanded;
};

collapse = function(e) {
  e.stopPropagation();
  isExpanded = false;
  return console.log(`collapse isEXpanded = ${isExpanded}`);
};
</script>

<div
  divider
  layout="row"
  class:over={isOver}
  class:hover={isHover}
  on:drop={drop}
  on:dragover={over}
  on:dragleave={leave}
    on:mouseenter={hover}
    on:mouseleave={unhover}
    class:expanded={isExpanded ? "expanded" : false}
>
  <div spacer flex="initial"/>
  <Indent level={indentation} flex="initial"/>
  <div flex
    class:expanded={isExpanded ? "expanded" : false}
    class:over={isOver}
    on:click={  toggleExpanded}
    class="indented"
  >
    {#if isExpanded}
      <div class="add-divider"
        in:slide="{{delay: 150, duration: 300, easing: sineIn }}"
        class:invalid={isInvalid}
         
         layout="row"
      >
        <button layout="row" on:click={add}>
          <div flex="initial" icon><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M12,20C7.59,20 4,16.41 4,12C4,7.59 7.59,4 12,4C16.41,4 20,7.59 20,12C20,16.41 16.41,20 12,20M12,2A10,10 0 0,0 2,12A10,10 0 0,0 12,22A10,10 0 0,0 22,12A10,10 0 0,0 12,2M13,7H11V11H7V13H11V17H13V13H17V11H13V7Z"/></svg></div>
          <div flex>Click to Add New List Item</div>          
        </button>
        <button layout="row"  on:click={collapse}>
          <div flex="initial" icon><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M12,2A10,10 0 0,1 22,12A10,10 0 0,1 12,22A10,10 0 0,1 2,12A10,10 0 0,1 12,2M12,4A8,8 0 0,0 4,12C4,13.85 4.63,15.55 5.68,16.91L16.91,5.68C15.55,4.63 13.85,4 12,4M12,20A8,8 0 0,0 20,12C20,10.15 19.37,8.45 18.32,7.09L7.09,18.32C8.45,19.37 10.15,20 12,20Z"/></svg></div>
          <div flex>Cancel</div>                  
        </button>

      </div>
    {/if}
      <div class="droptarget"
        class:invalid={isInvalid}>
        Drop to Move List Item Here
      </div>


  </div>
</div>


<style lang="sass">div[divider] {
  height: 8px;
  transition: height 0.15s ease-out;
  border-radius: 4px; }
  div[divider] .indented {
    border-radius: 4px;
    background: transparent; }
  div[divider]:hover .indented {
    background: #f1f1f1; }
  div[divider].over {
    height: 24px;
    transition: height 0.15s ease-in; }
    div[divider].over .indented {
      background: #f1f1f1; }
  div[divider].expanded {
    height: 40px;
    transition: height 0.15s ease-in; }
  div[divider] .expanded {
    padding: 8px 0px;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
    height: 40px; }
    div[divider] .expanded .indented {
      background: transparent; }
  div[divider] .droptarget {
    height: 100%;
    color: transparent;
    line-height: 24px; }
  div[divider] .indented.over .droptarget {
    padding: 0px 8px;
    color: var(--light-gray) !important;
    background: transparent;
    border-radius: 4px; }
  div[divider] .add-divider {
    text-align: left;
    border-radius: 3px;
    line-height: 24px;
    vertical-align: middle; }
    div[divider] .add-divider [icon] {
      padding-right: 8px;
      color: var(--green); }
      div[divider] .add-divider [icon] svg {
        fill: var(--green); }
  div[divider].invalid .placeholder {
    background: var(--red) !important; }
  div[divider] div[spacer] {
    width: 24px; }

/*# sourceMappingURL=NestableDivider.svelte.css.map */</style>
