<script lang="coffeescript">var destroyDispatcher, dispatch, goToItem, message, style, updateListItem;

import {
  onMount,
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import Indent from "./Indent.svelte";

import NestedItem from "../Nested/NestedItem.svelte";

import QuickEntry from "../../_Common/QuickEntry/GenericItem.svelte";

import InlineInput from '../../../components/Form/InlineInput/InlineInput.svelte';

import Menu from '../../_Common/Menus/ListItem.svelte';

import ProgressPopover from '../../_Common/Popovers/Progress.svelte';

import DueDatePopover from '../../_Common/Popovers/DueDate.svelte';

export var debug = false;

export var list = void 0;

export var listId = void 0;

message = "";

style = ":hover {font-weight: bold}";

goToItem = function(childItem) {
  return location.href = `#/lists/${item.id}`;
};

updateListItem = function(e) {
  if (listId != null) {
    console.log(`top event listid: ${listId}`);
    e.detail["list_id"] = listId;
  }
  return dispatch("update", e.detail);
};

destroyDispatcher = function(e) {
  var showDeleteConfirmation;
  dispatch("destroy", e.detail);
  return showDeleteConfirmation = false;
};

// debug = true
</script>


{#each list.radar_items as listItem, index}
  {#if listItem.has_due_dates()}
    <NestedItem item={listItem} index={index} on:update={updateListItem}>
      <div layout="row" type="list-item">
        <Indent level={listItem.ancestry.length}/>
        <div flex="initial" section="progress">
          <ProgressPopover item={listItem} bind:value={listItem.progress} on:update={updateListItem} on:open on:close idAttribute="uuid"/>
        </div>

        <div flex ellipsis main>
          <InlineInput bind:value={listItem.name} on:update={updateListItem({ detail: { uuid: listItem.uuid, ancestry: listItem.ancestry, name: listItem.name  } })} on:clicked={goToItem(listItem)} {style} let:label>
            <span ellipsis><a href="#/lists/{listItem.id}" on:click|preventDefault><em>{label || "No Name"}</em></a></span>
          </InlineInput>
          {#if debug }
            <div>uuid {listItem.uuid}</div>
            <div>index {listItem.index}</div>
            <div>ancestry {JSON.stringify(listItem.ancestry)}</div>
            <div>children: {listItem.radar_items.length}</div>
            <div>Has due date children: {listItem.has_due_dates()}</div>
          {/if}
        </div>
        <div flex="initial">
          <div layout="row">
            <!-- due date menu -->
            <div flex="initial" section="due-date" present={listItem.due_date != null}>
              <DueDatePopover item={listItem} value={listItem.due_date} on:update={updateListItem} on:open on:close idAttribute="uuid"/>
            </div>
          </div>
        </div>

      </div>

      <div slot="menu">
        <Menu item={listItem} on:update={updateListItem} on:destroy={destroyDispatcher}/>
      </div>

    </NestedItem>



    <!-- children -->
    {#if listItem.is_expanded_radar }
      <svelte:self bind:list={listItem}  on:update={updateListItem} on:destroy={destroyDispatcher}/>
    {/if}
  {/if}

{/each}

